@import url(http://tom.ille-web.de/css/constants.css);
@import url(http://tom.ille-web.de/css/constants.css);
@import url(http://tom.ille-web.de/css/default.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.default-header {
  background-color: var(--highlight-color);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: left;
          justify-content: left;
  font-size: calc(10px + 2vmin);
  color: var(--background-color);
}

a.portfolio-link {
  font-weight: 300;
  padding: 0 15px;
  text-align: center;
  color: var(--background-color);
}
a.portfolio-link:hover {
  color: var(--background-color);
  text-decoration: underline;
}

nav.navbar {
  padding: 0;
}

nav.navbar .container-fluid {
  padding: 0;
}

.logo {
  width: 28px;
  height: auto;
  vertical-align: middle;
  margin: 5px 15px 10px 15px;
}


.main-alg-element {
  width: 75%;
  -webkit-flex: 0 0 75%;
          flex: 0 0 75%;
  height: 100%;
  padding: 15px;
}

.settings-slider {
  padding-bottom: 0.5rem;
}

hr.settings-menu {
  background-color: var(--highlight-color);
  width: 75%;
  margin-top: 0.25rem;
}

.MuiSlider-root {
  color: var(--highlight-color) !important;
}

.MuiSwitch-colorSecondary.Mui-checked {
  color: var(--highlight-color) !important;
}

.MuiSwitch-colorSecondary.Mui-checked + .MuiSwitch-track {
  background-color: var(--highlight-color) !important;
}

.algorithm-link {
  margin-bottom: 0.5rem;
}

@media only screen and (max-width: 1300px) {
  .main-alg-element {
    width: 100%;
    -webkit-flex: 0 0 100%;
            flex: 0 0 100%;
    -webkit-order: 2;
            order: 2;
    height: 100vh;
    padding-bottom: 3rem;
  }
}

.point-distribution-map {
  height: 100%;
}

#canvas {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 0.5;
          flex: 1 0.5;
  cursor: crosshair;
}

.alg-menu {
  width: 25%;
  -webkit-flex: 0 0 25%;
          flex: 0 0 25%;
}

@media only screen and (max-width: 1300px) {
  .alg-menu {
    width: 100%;
    -webkit-flex: 0 0 100%;
            flex: 0 0 100%;
    -webkit-order: 1;
            order: 1;
  }
}

.alg-body {
  max-height: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

html,
body,
#root,
.container,
.App {
  height: 100vh;
  max-height: 100vh;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.default-algorithm-title {
  -webkit-flex: 0 1;
          flex: 0 1;
}

.alg-menu {
  padding: 15px;
}

.alg-row {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0;
}

